/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html {
  font-size: 10px;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
  font-size: 10px;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@-webkit-keyframes BackgroundGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes BackgroundGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-webkit-keyframes SocialHover {
  0% {
    color: #FFF;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    color: #FF0000;
    -webkit-transform: scale(1.1, 1.1);
    transform: scale(1.1, 1.1);
  }
  100% {
    color: #FFF;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
}
@keyframes SocialHover {
  0% {
    color: #FFF;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    color: #FF0000;
    -webkit-transform: scale(1.1, 1.1);
    transform: scale(1.1, 1.1);
  }
  100% {
    color: #FFF;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
}
@-webkit-keyframes SendButtonHover {
  0% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(1.1, 1.1);
    transform: scale(1.1, 1.1);
  }
  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
}
@keyframes SendButtonHover {
  0% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(1.1, 1.1);
    transform: scale(1.1, 1.1);
  }
  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
}
body {
  background: -webkit-linear-gradient(135deg, #404041, #000);
  background: -o-linear-gradient(135deg, #404041, #000);
  background: linear-gradient(-45deg, #404041, #000);
  background-attachment: fixed;
  background-size: 400% 400%;
  color: #fff;
  font-family: "Quicksand", sans-serif;
  font-weight: 300;
  height: 90vh;
  width: 100wh;
  -webkit-animation: "BackgroundGradient" 10s ease infinite;
  animation: "BackgroundGradient" 10s ease infinite;
}
body .container {
  margin: 0 auto;
  max-width: 1024px;
}
body .container header {
  padding: 1% 0;
}
body .container header nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
body .container header nav button, body .container header nav a {
  background: none;
  border: none;
  color: #FFF;
  cursor: pointer;
  margin-left: 2%;
  outline: none;
}
body .container header nav button:hover, body .container header nav a:hover {
  -webkit-animation: "SocialHover" 1s ease infinite;
  animation: "SocialHover" 1s ease infinite;
}
body .homeScreen .logo {
  display: block;
  height: 83vh;
  margin: 0 auto;
  width: auto;
}
body .homeScreen .projects {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
body .homeScreen .projects .project-container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1rem;
  line-height: 1.5rem;
  padding: 1% 1% 15%;
  width: 33.333%;
}
body .homeScreen .projects .project-container iframe {
  display: block;
  height: 66%;
  margin: 0 0 3% 0;
  width: 100%;
}
body .homeScreen .projects .project-container a {
  color: #1a73e8;
  font-weight: 700;
  text-decoration: none;
}
body .contactScreen h1 {
  font-size: 5vw;
  text-align: center;
}
body .contactScreen form {
  margin: 0 auto;
  width: 300px;
}
body .contactScreen form input, body .contactScreen form textarea {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: none;
  display: block;
  font-family: "Chathura", sans-serif;
  font-size: 35px;
  margin-bottom: 10px;
  outline: none;
  padding: 2px 10px;
  width: 100%;
}
body .contactScreen form textarea {
  resize: none;
}
body .contactScreen form input[type=submit] {
  border-radius: 4px;
  background-color: #1a73e8;
  color: #FFF !important;
  cursor: pointer;
  font-weight: 700;
  text-transform: uppercase;
}
body .contactScreen form input[type=submit]:hover {
  -webkit-animation: "SendButtonHover" 1s ease infinite;
  animation: "SendButtonHover" 1s ease infinite;
}
body footer {
  font-size: 1rem;
  padding: 5% 0;
  text-align: center;
}



